/* src/assets/css/PlayerCard.css */

.seasons{
  margin-left: 0.8rem;
}
.search-result {
  display: flex;
  flex-wrap: wrap;
  justify-content: center; /*   */
  gap: 10px; /*     */
}

.player-card {
  width: 300px; /*    */
  border: 1px solid #ccc; /*    */
  border-radius: 10px; /*     */
  overflow: hidden; /*       */
  text-align: center; /*    */
  margin: 10px; /*    */
  padding: 10px; /*    */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /*   */

  &:hover {
    transform: scale(1.05); /*     */
  }
}

.player-header {
  display: flex; /*      */
  align-items: center; /*      */
  justify-content: center; /*      */
  gap: 3px; /*      */
}

.player-card h3 {
  font-size: 1.2em; /*     */
  margin-bottom: 10px; /*     */
}

.player-card .season-image {
  width: 30px; /*     */
  height: auto; /*       */
  margin-bottom: 10px;
}

.player-card .player-profile-image {
  width: 100px; /*     */
  height: 100px; /*     */
  border-radius: 50%; /*    */
  object-fit: cover; /*      */
  margin-bottom: 5px; /*      */
}

.player-card .ProfileCard-description {
  font-size: 0.9em; /*     */
  margin-bottom: 5px; /*       */
}

.player-card img {
  width: 100%; /*      */
  height: auto; /*      */
  object-fit: cover; /*     */
}

.player-card .enhancement-image {
  width: 50px; /*     */
  height: auto; /*       */
  margin-top: 10px; /*      */
}

.player-card .player-info p {
  margin: 1.5px; /*     */
}

.player-card .player-stats {
  display: flex; /*    */
  justify-content: space-around; /*     */
  margin-top: 10px; /*       */
  flex-wrap: wrap; /*     */
}

.player-card .goalkeeper-stats,
.player-card .field-player-stats {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.player-card .stat {
  display: flex; /*     */
  flex-direction: column; /*      */
  align-items: center; /*      */
  font-size: 0.9em; /*     */
  margin: 5px; /*     */
}

.player-card .stat strong {
  font-size: 0.8em; /*      */
  color: #777; /*     */
}

.player-card .stat span {
  font-size: 1.2em; /*      */
  font-weight: bold; /*     */
}

